$desktop-screen: 768px
    
.thumbtack
    width: 50px
    height: 50px
    position: absolute
    z-index: 12
    @media screen and (min-width: $desktop-screen) 
        width: 100px
        height: 100px

.thumbtack-front
    clip-path: circle(14.6% at 50% 50%)
    background-color: red
    position: absolute
    left: 1.35px
    top: 1.1px
    width: 12.5px
    height: 125px
    @media screen and (min-width: $desktop-screen) 
        left: 2.7px
        top: 2.2px
        width: 25px
        height: 25px

.thumbtack-shadow
    clip-path: circle(14.6% at 50% 50%)
    background-color: rgb(81, 73, 73)
    position: absolute
    margin-left: 1px
    z-index: 3
    width: 15px
    height: 15px
    @media screen and (min-width: $desktop-screen) 
        margin-left: 2px
        width: 30px
        height: 30px

.thumbtack-back
    clip-path: circle(14.6% at 50% 50%)
    background-color: red
    position: absolute
    margin-top: 1.5px
    z-index: 2
    width: 15px
    height: 15px
    @media screen and (min-width: $desktop-screen) 
        margin-top: 3px
        width: 30px
        height: 30px

.thumbtack-needle
    width: 5px
    height: 1px
    background-color: black
    rotate: -56deg
    margin-top: 10px
    margin-left: 4px
    z-index: 1
    @media screen and (min-width: $desktop-screen)  
        width: 10px
        height: 1px
        margin-top: 20px
        margin-left: 8px