#background-wrap {
  bottom: 0;
  left: 0;
  padding-top: 50px;
  position: fixed;
  right: 0;
  top: 0;
  z-index: -1;
}

/* KEYFRAMES */
@keyframes animateCloud {
  0% {
    margin-left: -1000px;
  }
  100% {
    margin-left: 100%;
  }
}
@keyframes animateCloudReverse {
  0% {
    margin-left: 100%;
  }
  100% {
    margin-left: 50%;
  }
}
/* ANIMATIONS */
.x1 {
  animation: animateCloud 35s linear infinite;
  transform: scale(0.65);
}

.x2 {
  animation: animateCloud 20s linear infinite;
  transform: scale(0.3);
}

.x3 {
  animation: animateCloud 30s linear infinite;
  transform: scale(0.5);
}

.x4 {
  animation: animateCloud 18s linear infinite;
  margin-top: 100px;
  transform: scale(0.4);
}

.x5 {
  animation: animateCloud 25s linear infinite;
  margin-top: 200px;
  transform: scale(1.45);
  z-index: 999;
}

.x6 {
  animation: animateCloud 25s linear infinite;
  margin-top: 200px;
  transform: scale(1.45);
  z-index: 999;
}

/* OBJECTS */
.cloud {
  background: #fff;
  background: linear-gradient(top, #fff 5%, #f1f1f1 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#fff", endColorstr="#f1f1f1",GradientType=0 );
  border-radius: 100px;
  box-shadow: 0 8px 5px rgba(0, 0, 0, 0.1);
  height: 120px;
  position: relative;
  width: 350px;
}
.cloud:after, .cloud:before {
  background: #fff;
  content: "";
  position: absolute;
  z-index: -1;
}
.cloud:after {
  border-radius: 100px;
  height: 100px;
  left: 50px;
  top: -50px;
  width: 100px;
}
.cloud:before {
  border-radius: 200px;
  width: 180px;
  height: 180px;
  right: 50px;
  top: -90px;
}/*# sourceMappingURL=Cloud.css.map */